.services {
	background: #1D1D1D url(../img/bg-sverl.jpg) no-repeat top right;
	padding: 120px 0;
	&__wrap {
		display: flex;
		justify-content: space-between; }
	&__form {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin-top: 20px;
		&-block {
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			min-width: 425px;
			width: 425px;
			padding: 45px;
			padding-bottom: 140px;
			background: #F6F6F9 url(../img/img-form.png) no-repeat center bottom;
			border-radius: 15px;
			text-align: center;
			margin-right: 20px;
			&::before {
				content: 'Бестраншейные переходы';
				position: absolute;
				top: -17px;
				left: calc(50% -136px);
				padding: 4px 16px;
				background: #FA0D0D;
				border-radius: 19px;
				font-weight: bold;
				font-size: 18px;
				color: #FFFFFF; } }
		&-title {
			font-weight: bold;
			font-size: 36px;
			color: #0F2137; }
		&-text {
			font-weight: 300;
			font-size: 16px;
			color: #292929; }
		&-input {
			margin-top: 34px;
			&:first-child {
				margin-top: 0; } }
		&-btn {
			margin-top: 28px; } }
	&__block {
		margin-top: 40px; }
	&__title {
		font-size: 56px;
		line-height: 70px;
		color: #FFFFFF;
		margin: 25px 0; }
	&__text {
		max-width: 400px;
		font-weight: 300;
		font-size: 18px;
		color: #FFFFFF; }
	&__btn {
		margin-top: 50px; } }

@media (min-width: 1900px ) {
	.services {
		background-size: cover; } }

@media (max-width: 900px ) {
	.services {
		padding-top: 62px;
		padding-bottom: 100px;
		&__form-block {
			display: none; }
		&__block {
			margin-top: 0; }
		&__title {
			font-size: 36px;
			line-height: 45px;
			margin: 20px 0; }
		&__text {
			font-size: 16px; }
		&__btn {
			margin-top: 200px;
			span {
				display: none; } } } }
