.question {
	background: url(../img/bg-quest.png) no-repeat top center;
	padding-top: 95px;
	padding-bottom: 30px;
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center; }
	&__title {
		font-size: 56px;
		line-height: 70px;
		text-align: center;
		color: #0F2137; }
	&__block {
		max-width: 520px; } }

@media (max-width: 900px ) {
	.question {
		padding-top: 65px;
		padding-bottom: 200px;
		&__title {
			font-size: 34px;
			line-height: 45px;
			margin-top: 10px;
			margin-bottom: 20px; } } }

@media (max-width: 693px ) {
	.question {
		padding-bottom: 270px; } }
