.navigation {
	padding: 12px 0;
	z-index: 10;
	&__address {
		display: flex;
		justify-content: flex-end;
		font-size: 16px;
		color: #fff; }
	&__info {
		&-block,
		&-wrap {
			display: flex;
			align-items: center;
			flex-wrap: wrap; }
		&-wrap {
			margin-right: 15px; }
		&-block {
			padding-top: 6px;
			&--move {
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				padding: 12px 0;
				background: #171919;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
				z-index: 10;
				.navigation__info-block {
					width: 1150px;
					margin: 0 auto; } } }
		&-wrap span {
			display: block;
			height: 41px;
			width: 1px;
			background: #C4C4C4;
			opacity: 0.2;
			margin: 0 18px; }
		&-wrap p {
			font-size: 14px;
			line-height: 100%;
			color: #fff;
			margin: 0; } }
	&__logo {
		margin-right: 7px;
		&-text {
			font-weight: 900;
			font-size: 18px;
			line-height: 86.2%;
			color: #fff; } }
	&__mail {
		font-weight: bold;
		font-size: 16px;
		line-height: 28px;
		color: #3671E9;
		margin-left: 30px; }
	&__whatsapp {
		display: flex;
		align-items: center;
		margin-left: auto;
		span {
			font-weight: bold;
			font-size: 30px;
			line-height: 28px;
			color: #FFFFFF;
			margin-left: 15px; } }
	&__btn {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 14px 30px;
		background: #FAB20D;
		border-radius: 50px;
		margin-left: 30px;
		font-weight: bold;
		font-size: 16px;
		color: #0D0D2B; }
	&__mobile-btn {
		position: relative;
		display: none;
		width: 24px;
		height: 24px;
		padding: 0;
		border: none;
		background: transparent;
		margin-left: 15px;
		z-index: 10;
		cursor: pointer;
		span {
			display: block;
			&,
			&::before,
			&::after {
				content: '';
				position: absolute;
				top: calc(50% - 1px);
				left: calc(50% - 9px);
				width: 18px;
				height: 1px;
				border: 1px solid #FFFFFF;
				border-radius: 2px;
				transition: .2s; }
			&::before {
				transform: translateY(5px); }
			&::after {
				transform: translateY(-5px); } }
		&--active {
			span {
				border-color: transparent;
				&::before {
					transform: rotate(45deg); }
				&::after {
					transform: rotate(-45deg); } } } }
	&__list {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		width: 100%;
		margin-top: 35px;
		&--move {
			margin-top: 108px; } }
	&__link {
		font-weight: bold;
		font-size: 17px;
		line-height: 28px;
		color: #FFFFFF; }
	&__nav {
		&-wrap {
			display: none;
			flex-direction: column;
			align-items: center;
			width: calc(100% + 40px);
			padding-bottom: 88px;
			margin: 0 -20px;
			background: #222222; }
		&-text {
			font-size: 16px;
			line-height: 28px;
			text-align: center;
			color: #FFFFFF;
			margin: 0;
			margin-top: 37px; }
		&-whatsapp {
			font-weight: bold;
			font-size: 30px;
			line-height: 28px;
			color: #FFFFFF;
			margin-top: 40px; }
		&-mail {
			font-weight: bold;
			font-size: 16px;
			line-height: 28px;
			color: #3671E9;
			margin-top: 10px; }
		&-btn {
			margin: 0;
			margin-top: 25px; } } }

@media (max-width: 1200px) {
	.navigation__info-block--move {
		.navigation__info-block {
			width: auto;
			margin: 0 15px; } } }

@media (max-width: 1100px ) {
	.navigation {
		&__logo {
			align-self: flex-start; }
		&__info-wrap {
			flex-direction: column;
			align-items: flex-start;
			span,	a br {
				display	: none; }
			p {
				margin-top: 10px; } }
		&__mail {
			margin-left: 0;
			margin-top: 5px; } } }

@media (max-width: 900px ) {
	.navigation {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		padding: 12px 20px;
		background: #171919;
		&__address,
		&__mail,
		&__btn,
		&__nav,
		&__whatsapp span {
			display: none; }
		&__info {
			&-block {
				flex-wrap: nowrap;
				margin-top: 0;
				&--move {
					padding: 0; } }
			&-wrap {
				margin-right: 5px;
				p {
					font-size: 12px; } } }
		&__logo-text {
			font-size: 16px; }
		&__whatsapp img {
			width: 32px; }
		&__mobile-btn {
			display: block; }
		&__nav--active {
			position: fixed;
			top: 0;
			left: 0;
			padding: 20px;
			padding-bottom: 0;
			background: #171919;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 100%;
			height: 100%;
			overflow-y: scroll;
			.navigation {
				&__list {
					flex-direction: column;
					width: 100%; }
				&__item {
					display: flex;
					width: 100%; }
				&__link {
					width: 100%;
					padding: 10px;
					text-align: center; }
				&__nav-wrap {
						display: flex; } } } } }
