.about {
	padding: 100px 0;
	background: url(../img/bg-about.png) no-repeat right calc(100% + 100px);
	&__wrap {
		display: flex;
		align-items: flex-start; }
	&__img {
		margin-top: 30px;
		margin-right: 55px;
		flex-shrink: 2;
		img {
			max-width: 100%; } }
	&__block {
		min-width: 450px; }
	&__icon {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		font-weight: bold;
		font-size: 30px;
		line-height: 38px;
		color: #3B6AA4;
		img {
			margin-right: 23px; } }
	&__title {
		font-size: 56px;
		line-height: 70px;
		color: #0F2137;
		margin-bottom: 18px; }
	&__text {
		font-weight: 300;
		font-size: 18px;
		color: #0F2137;
		margin-bottom: 20px;
		max-width: 510px; }
	&__info {
		background: #030303 url(../img/bg-site-comp.jpg) no-repeat center;
		background-size: cover;
		&-block {
			position: relative;
			max-width: 909px;
			background: linear-gradient(218.36deg, rgba(251, 186, 13, 0.8) 12.68%, rgba(245, 152, 12, 0.8) 42.56%, rgba(239, 119, 12, 0.8) 79.08%, rgba(235, 99, 11, 0.8) 110.62%, rgba(234, 92, 11, 0.8) 130.54%);
			padding: 65px 72px; }
		&-text {
			font-weight: bold;
			font-size: 22px;
			color: #FFFFFF;
			margin-bottom: 25px;
			&:last-child {
				margin-bottom: 0; } }
		&-btn {
			position: absolute;
			top: calc(50% - 30px);
			right: -110px; } } }

@media (max-width: 1050px ) {
	.about__info-btn {
		top: auto;
		bottom: -30px;
		right: calc(50% - 110px); } }

@media (max-width: 900px ) {
	.about {
		padding-top: 50px;
		padding-bottom: 20px;
		&__wrap {
			flex-direction: column-reverse;
			justify-content: center;
			align-items: center; }
		&__img {
			margin-top: 30px;
			margin-right: 0; }
		&__block {
			min-width: auto;
			text-align: center; }
		&__icon {
			justify-content: center;
			font-size: 18px;
			line-height: 23px;
			img {
				margin-right: 23px; } }
		&__title {
			font-size: 36px;
			line-height: 45px;
			margin-bottom: 18px; }
		&__text {
			font-size: 16px; }
		&__info {
			&-block {
				padding: 33px 15px;
				padding-bottom: 40px;
				margin: 0 -15px; }
			&-text {
				font-size: 18px; } } } }

@media (max-width: 400px ) {
	.about__icon {
		flex-direction: column;
		img {
			margin-right: 0; } } }
