.header {
	background-color: #171919;
	background-image: url(../img/bg-header-2.png) , url(../img/bg-header.jpg);
	background-repeat: no-repeat;
	background-position: calc(50% - 300px) top , right top;
	padding-bottom: 130px;
	&__block {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		padding-top: 120px; }
	&__comp {
		background: rgba(255, 255, 255, 0.1);
		border-radius: 32px;
		padding: 6px 6px 6px 19px;
		margin: 0;
		font-size: 16px;
		color: #fff;
		span {
			background: #FAB20D;
			border-radius: 32px;
			padding: 4px 16px;
			margin-left: 7px;
			font-weight: bold; } }
	&__title {
		font-size: 64px;
		line-height: 125%;
		color: #FFFFFF;
		margin: 24px 0; }
	&__text {
		font-size: 20px;
		color: #fff; }
	&__btn {
		display: flex;
		align-items: center;
		background: rgba(255, 255, 255, 0.2);
		border-radius: 40px;
		padding: 10px  25px 8px 13px;
		margin-top: 45px;
		font-weight: bold;
		font-size: 18px;
		line-height: 122.5%;
		color: #FFFFFF;
		img {
			margin-right: 12px; } }
	&__send {
		display: none;
		margin-top: 55px; }
	&__total {
		&-list {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			margin-top: 230px; }
		&-item {
			display: flex;
			align-items: center;
			&:first-child .header__total-img {
				padding-top: 20px; } }
		&-img {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 80px;
			height: 80px;
			background: rgba(255, 255, 255, 0.1);
			border-radius: 50%;
			margin-right: 25px; }
		&-text {
			font-weight: bold;
			font-size: 50px;
			color: #3671E9;
			span {
				font-weight: 400;
				font-size: 36px;
				color: #fff; } }
		&-text-2 {
			font-weight: bold;
			font-size: 18px;
			line-height: 122.5%;
			color: #FFFFFF; } } }

@media (min-width: 1900px ) {
	.header {
		background-size: auto, cover; } }

@media (max-width: 1000px ) {
	.header__total {
		&-list {
			justify-content: center;
			margin-top: 90px; }
		&-item {
			margin: 10px; } } }

@media (max-width: 900px ) {
	.header {
		padding-bottom: 55px;
		&__comp {
			font-size: 12px;
			padding: 4px 4px 4px 10px;
			margin-left: -5px;
			margin-right: -10px;
			span {
				padding: 4px 8px; } }
		&__title {
			font-size: 36px;
			margin: 13px 0; }
		&__text {
			font-size: 16px; }
		&__btn {
			padding: 10px 25px 8px 13px;
			margin-top: 24px;
			font-size: 16px;
			img {
				margin-right: 12px; } }
		&__send {
			display: flex; }
		&__total {
			&-list {
				flex-direction: column;
				justify-content: center;
				margin-top: 230px; }
			&-item {
				margin: 10px; }
			&-img {
				margin-right: 20px; }
			&-text {
				font-size: 36px;
				span {
					font-size: 24px; } }
			&-text-2 {
				font-size: 16px; } } } }
