.certificates {
	padding-top: 180px;
	&__wrapper {
		display: flex;
		align-items: flex-end; }
	&__block {
		margin-right: 20px;
		&-right {
			max-width: 595px;
			margin-right: auto;
			margin-left: auto; } }
	&__img {
		margin-bottom: -10px; }
	&__title {
		font-size: 56px;
		line-height: 70px;
		color: #0F2137; }
	&__subtitle {
		font-weight: bold;
		font-size: 24px;
		line-height: 130.2%;
		color: #3671E9; }
	&__text {
		font-weight: 300;
		font-size: 18px;
		color: #0F2137;
		margin-top: 20px; }
	#certificates-carousel {
		margin-top: 60px; } }

@media (max-width: 900px ) {
	.certificates {
		padding-top: 90px;
		&__wrapper {
			flex-direction: column;
			align-items: center;
			text-align: center; }
		&__img {
			display: block;
			margin: auto; }
		&__title {
			font-size: 36px;
			line-height: 45px; }
		&__subtitle {
			margin-top: 15px; }
		#certificates-carousel {
			margin-top: 40px; } } }
