body {
	min-width: 320px;
	margin: 0;
	padding: 0;
	font-family: 'Cera Pro', sans-serif;
	line-height: 1.5;
	background: #ffffff; }

*, *:before, *:after {
	box-sizing: border-box;
	outline: none;
	text-decoration: none;
	margin: 0;
	padding: 0; }

iframe {
	width: 100%; }

ul {
	list-style: none; }

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden; }

.global-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 276px;
	height: 64px;
	padding-left: 20px;
	background: #3671E9;
	border: none;
	border-radius: 32px;
	font-weight: bold;
	font-size: 20px;
	color: #FFFFFF;
	cursor: pointer;
	span {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 32px;
		height: 32px;
		background: #fff;
		border-radius: 50%;
		margin-left: 24px; } }

.global-btn--no-img {
	display: inline-block;
	padding: 16px 32px;
	background: #3671E9;
	border: none;
	border-radius: 32px;
	font-weight: bold;
	font-size: 18px;
	color: #FFFFFF;
	text-align: center;
	cursor: pointer; }

.global-input {
	background: #fff;
	width: 100%;
	max-width: 328px;
	padding: 22px 36px;
	border: none;
	border-radius: 40px;
	font-weight: bold;
	font-size: 18px;
	color: #0F2137; }
