.customers {
	background: url(../img/bg-customers.png) no-repeat bottom right;
	padding: 120px 0;
	&__title {
		font-size: 56px;
		line-height: 70px;
		color: #0F2137; }
	&__text {
		font-size: 24px;
		color: #3671E9;
		margin-top: 19px; }
	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		margin-top: 40px; }
	&__block {
		width: 48%; }
	&__partner {
		position: relative;
		display: block;
		font-weight: bold;
		font-size: 24px;
		cursor: pointer;
		margin-top: 20px;
		margin-bottom: 10px;
		padding-left: 25px;
		&::before {
			content: '+';
			position: absolute;
			left: 0; }
		span {
			display: block;
			font-weight: 300; }
		&-text {
			display: none;
			font-weight: 300;
			font-size: 18px;
			padding-left: 25px; } }
	&__label input:checked ~ .customers__partner::before {
		content: '-'; }
	&__label input:checked ~ .customers__partner-text {
			display: block; }
	&__more {
		display: none;
		font-size: 18px;
		color: #3671E9;
		padding-left: 25px;
		cursor: pointer; }
	#customers-carousel {
		margin-top: 86px; } }

@media (min-width: 1900px ) {
	.customers {
		background-size: cover; } }

@media (max-width: 900px ) {
	.customers {
		background-position: bottom center;
		padding-top: 62px;
		padding-bottom: 80px;
		&__img {
			display: block;
			margin: auto; }
		&__title {
			font-size: 36px;
			line-height: 45px;
			text-align: center;
			margin-top: 10px; }
		&__text {
			font-size: 18px;
			text-align: center;
			margin-top: 10px; }
		&__wrapper {
			flex-direction: column;
			margin-top: 20px; }
		&__block {
			width: 100%;
			&:last-child {
				display: none; } }
		&__more {
			display: block; }
		#customers__more:checked ~ .customers__block {
			display: block;
			margin-top: -10px; }
		#customers__more:checked ~ .customers__more {
			display: none; }
		#customers-carousel {
			margin-top: 40px; } } }
