.advantages {
	background: #1D1D1D url(../img/bg-hadv.jpg) no-repeat top right;
	padding-top: 120px;
	padding-bottom: 140px;
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center; }
	&__title {
		font-size: 56px;
		line-height: 70px;
		text-align: center;
		color: #FFFFFF; }
	&__desc {
		font-size: 18px;
		line-height: 28px;
		text-align: center;
		color: #E0E0E0;
		max-width: 540px;
		margin-top: 20px; }
	&__list {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin-top: 60px;
		margin-bottom: 100px; }
	&__img {
		margin-bottom: 20px; }
	&__text {
		font-weight: bold;
		font-size: 24px;
		line-height: 30px;
		color: #FFFFFF;
		&-small {
			font-size: 18px;
			line-height: 23px;
			color: #FFFFFF;
			max-width: 370px;
			margin-top: 10px; } } }

@media (min-width: 1900px ) {
	.advantages {
		background-size: cover; } }

@media (max-width: 1170px ) {
	.advantages {
		background-size: cover; } }

@media (max-width: 1080px ) {
	.advantages {
		&__list {
			flex-direction: column; }
		&__item {
			margin-top: 45px; } } }

@media (max-width: 900px ) {
	.advantages {
		&__title {
			font-size: 36px;
			line-height: 45px; }
		&__desc {
			font-size: 16px;
			line-height: 28px;
			margin-top: 20px; }
		&__list {
			margin-top: 60px;
			margin-bottom: 100px; }
		&__img {
			margin-bottom: 15px; }
		&__text {
			font-size: 18px;
			line-height: 23px;
			&-small {
				font-size: 16px;
				line-height: 20px;
				max-width: 340px; } } } }
