.contacts {
	background: #1D1D1D url(../img/bg-footer.jpg) no-repeat top center;
	&__form {
		position: absolute;
		top: 73px;
		left: 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background: #F6F6F9;
		padding: 43px 50px;
		width: 100%;
		min-height: 194px;
		border-radius: 15px;
		box-shadow: 0 5px 10px rgba(#000, .1);
		&-wrap {
			position: relative;
			padding: 100px 0; }
		&-block {
			align-self: flex-start;
			min-width: 425px;
			&-b {
				display: flex;
				align-items: center;
				justify-content: space-between;
				flex-wrap: wrap;
				width: 100%; } }
		&-title {
			font-weight: bold;
			font-size: 36px;
			color: #0F2137; }
		&-text {
			font-weight: 300;
			font-size: 18px;
			line-height: 21px;
			color: #292929;
			br {
				display: none; } }
		&-input {
			margin: 10px 0; }
		&-btn {
			position: relative;
			margin: 10px 0;
			&::before {
				content: '*Нажимая кнопку я соглашаюсь с политикой персональных данных';
				position: absolute;
				top: calc(100% + 12px);
				left: calc(50% - 138px);
				width: 276px;
				font-weight: 300;
				font-size: 14px;
				line-height: 15px;
				text-align: center;
				color: #292929; } } }
	&__wrap {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
		align-items: flex-end;
		padding: 95px 0; }
	&__form-2 {
		background: #F6F6F9 url(../img/img-form-2.png) no-repeat bottom left;
		border-radius: 15px;
		padding: 44px;
		padding-bottom: 120px;
		max-width: 657px;
		&-title {
			font-weight: bold;
			font-size: 36px;
			color: #0F2137;
			margin-bottom: 10px; }
		&-text {
			font-weight: 300;
			font-size: 16px;
			line-height: 21px;
			color: #292929;
			margin-bottom: 20px; }
		&-input {
			margin-bottom: 34px; }
		&-btn {
			position: relative;
			&::before {
				content: '*Нажимая кнопку я соглашаюсь с политикой персональных данных';
				position: absolute;
				top: calc(100% + 12px);
				left: calc(50% - 138px);
				width: 276px;
				font-weight: 300;
				font-size: 14px;
				line-height: 15px;
				text-align: center;
				color: #292929; } } }
	&__contants {
		min-width: 400px;
		margin-right: 10px;
		&-text {
			font-weight: bold;
			font-size: 24px;
			color: #FFFFFF;
			margin-bottom: 36px;
			span {
				font-weight: 300; }
			a {
				color: #3671E9; } }
		&-wrap {
			display: flex;
			flex-direction: row-reverse;
			justify-content: space-between;
			align-items: center;
			margin-top: 50px; }
		&-block {
			display: flex;
			align-items: center;
			a {
				width: 40px;
				height: 40px;
				margin: 10px; } }
		&-btn {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 221px;
			height: 67px;
			background: #FAB20D;
			border-radius: 32px;
			font-weight: bold;
			font-size: 18px;
			color: #0D0D2B; } }
	&__footer {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding-bottom: 80px;
		img {
			max-width: 100%; } }
	&__card {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 35px;
		div {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 78px;
			height: 52px;
			background: rgba(#E0E0E0, .05);
			border-radius: 8px;
			margin: 10px; } }
	&__copyrights {
		font-size: 16px;
		line-height: 28px;
		text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		color: #fff;
		margin-top: 40px; } }

@media (max-width: 900px ) {
	.contacts {
		&__form {
			top: -130px;
			justify-content: center;
			flex-wrap: wrap;
			padding: 30px 10px;
			padding-bottom: 60px;
			&-wrap {
				padding: 100px 0; }
			&-block {
				min-width: auto;
				text-align: center;
				&-b {
					justify-content: center;
					margin-top: 10px; } }
			&-title {
				font-size: 34px;
				margin-bottom: 10px; }
			&-text br {
				display: block; }
			&-input,
			&-btn {
				margin: 10px; } }
		&__wrap {
			flex-direction: column;
			align-items: center;
			padding-top: 32px;
			padding-bottom: 44px; }
		&__form-2 {
			margin-bottom: 30px;
			&-title {
				font-size: 34px; } }
		&__contants {
			min-width: auto;
			margin-right: 0;
			&-wrap {
				flex-direction: column;
				align-items: center; }
			&-block {
				margin-bottom: 20px; } }
		&__footer {
			padding-bottom: 40px; } } }

@media (max-width: 693px ) {
	.contacts {
		&__form {
			top: -230px;
			&-block-b {
				flex-direction: column; }
			&-input,
			&-btn {
				margin: 10px 0; } }
		&__form-2 {
			padding: 30px 10px;
			padding-bottom: 120px;
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
			br {
				display: none; } } } }
